.toast-container {
    position: fixed;
    top: 10px;
    right: 30px;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: 200px;
    width: 350px;
  }
  
  .toast {
    background-color: rgba(255, 255, 255, 1);
    color: rgba(71, 68, 68, 1);
    padding: 15px 20px;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    opacity: 0;
    transform: translateY(-20px);
    transition: opacity 0.3s ease, transform 0.3s ease;
    font-family: "Arial", sans-serif;
    font-size: 16px;
  
    display: flex;
  
    align-items: center;
  
    align-items: center;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
  
    font-size: 20px;
    line-height: 30px;
  }
  .toast span {
    margin-left: 15px;
  }
  
  .toast.show {
    opacity: 1;
    transform: translateY(0);
  }
  
  .mover {
    background-color: rgb(96, 184, 75);
    height: 5px;
    margin-top: -5px;
    border-radius: 10px;
  }
  
  .mover_error {
    background-color: red;
    height: 5px;
  }
  .mover2.show {
    opacity: 0;
    transform: translateY(-10);
  }